import React, { Component } from 'react';
import { Modal, Button, notification } from 'antd';
import { DeleteOutlined } from '@ant-design/icons';

import { getUserToken } from '../../../store/user';
import { deleteSector } from '../../../store/sector';

const { confirm } = Modal;

class SectorDelete extends Component {

    constructor(props) {
        super(props)
    
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleSubmit() {
        const token = getUserToken();
        const sectorID = this.props.sector.key;
        const nameSector = this.props.sector.name;

        confirm ({
			title: 'Excluso de Setor',
			content: `Tem Certeza Que Deseja Excluir o Setor: ${ nameSector } ?`,
			okText: 'Sim',
			okType: 'danger',
			cancelText: 'No',
		
			onOk() {
                deleteSector(token, sectorID);
                notification.open({ 
                    type: 'success',
                    message: 'Setor Excludo',
                    description: 'Setor Excludo Com Sucesso!',
                });
                notification.open({
                    type: 'info',
                    message: 'Ao Requerida',
                    description: 'Por Favor, Atualize a Pgina!',
                });
			},
			onCancel() {
                notification.open({
                    type: 'info',
                    message: 'Ao Cancelada',
                    description: 'Excluso de Setor Cancelada Com Sucesso!',
                });
			},
		});
    }

    render() {
        return (
            <Button type = 'primary' onClick = { this.handleSubmit } danger>
                <DeleteOutlined /> <b> Excluir </b> 
            </Button>
        );
    }
}

export default SectorDelete;