import React, { Component } from 'react';
import { Button, Drawer, Descriptions, Badge } from 'antd';
import { AuditOutlined } from '@ant-design/icons';
import { StopOutlined } from '@ant-design/icons';

class Record extends Component {

    constructor(props) {
        super(props)
    
        this.state = {
            visible: false
        }

        this.showDrawer = this.showDrawer.bind(this);
        this.onClose = this.onClose.bind(this);
    }

    showDrawer = () => {
        this.setState({ visible: true });
    };

    onClose = () => {
		this.setState({ visible: false });
    };    

    render() {
        return (
            <span>
                <Button type = 'primary' ghost onClick = { this.showDrawer }>
                    <b><AuditOutlined /> Visualizar Ata </b>
                </Button>
                <Drawer
				    title = { `Ata da Reunio: ${ this.props.meeting.title }` }
					width = { '100%' }
					closable = { true }
					onClose = { this.onClose }
					visible = { this.state.visible }
					footer = { 
                        <div style = {{ textAlign: 'center' }}>
                            <Button onClick = { this.onClose } style = {{ marginRight: 8 }}>
                                <StopOutlined /> Fechar
                            </Button>
                        </div>
                    }
				>
                    <Descriptions 
						bordered column = {{ xxl: 4, xl: 3, lg: 3, md: 3, sm: 2, xs: 1 }}
					>
						<Descriptions.Item label = { <b> Ttulo </b> }>
							{ this.props.meeting.title }
						</Descriptions.Item>
                        <Descriptions.Item label = { <b> Lder da Reunio </b> }>
							{ this.props.meeting.meeting_leader }
						</Descriptions.Item>
						<Descriptions.Item label = { <b> Status da Reunio </b> }>
							<Badge status = 'success' text = { this.props.meeting.status } />
						</Descriptions.Item>
						<Descriptions.Item label = { <b> Horrio Programado da Reunio </b> }>
							{ this.props.meeting.initial_hour }
						</Descriptions.Item>
						<Descriptions.Item label = { <b> Horrio de Inicio Real da Reunio </b> }>
							{ this.props.meeting.real_hour }
						</Descriptions.Item>
                        <Descriptions.Item label = { <b> Durao da Reunio </b> }>
                            { this.props.meeting.duration_time }
                        </Descriptions.Item>
						<Descriptions.Item label = { <b> Data Programada da Reunio </b>}>
							{ this.props.meeting.initial_date }
						</Descriptions.Item>
						<Descriptions.Item label = { <b> Data Real da Reunio </b> } span = { 2 }>
							{ this.props.meeting.real_date }
						</Descriptions.Item>
                        <Descriptions.Item label = { <b> Ementa </b> } span = { 3 }>
							{ this.props.meeting.subject_matter }
						</Descriptions.Item>
                        <Descriptions.Item label = { <b> Usurios na Reunio </b> } span = { 3 }>
							<ul>
								{
									this.props.meeting.users.map(user => 
										<li key = { user.key }> { user.title } </li> 
									)
								}
							</ul>
						</Descriptions.Item>
                        <Descriptions.Item label = { <b> Pautas da Reunio </b> } span = { 3 }>
							<ul>
								{
									this.props.meeting.agendas.map(agenda => 
										<li key = { agenda.key }> { agenda.title } </li> 
									)
								}
							</ul>
						</Descriptions.Item>
						<Descriptions.Item label = { <b> Regras da Reunio </b> } span = { 3 }>
							<ul>
								{
									this.props.meeting.rules.map(rules => 
										<li key = { rules.key }> { rules.title } </li> 
									)
								}
							</ul>
						</Descriptions.Item>
                    </Descriptions>
                </Drawer>
            </span>
        );
    }
}

export default Record;